
@import "./../../variable";

#circle-section{
    position: absolute;
    background-color: cadetblue;
    border-radius: 50%;
    overflow: hidden;
    z-index: $circle-index;
}

.circle-fade-in-out{
    animation: fade-in-out-animate 2s ease ;
}


@keyframes fade-in-out-animate {
    0% {
        width: 0px;
        height: 0px;
        top: 0px;
        right: 0px;
    }
    50% {
        width: 300vw;
        height: 300vh;
        top: -150vh;
        right: -150vw;
    }
    100% {
        width: 0px;
        height: 0px;
        top: 0px;
        right: 0px;
    }
}