#intro-section{
    width: 100%;
    min-height: 100vh;
    background-color: darkcyan;
    display: flex;
    align-items: center;
    justify-content: center;

    .content{
        width: 90%;
        padding: 100px 0;
        margin: 0 auto;
        max-width: 1200px;
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        .avatar{
            background-color: wheat;
            width: 90vw;
            max-width: 300px;
            height: 300px;
            border-radius: 50%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            box-shadow: 1px 1px 12px rgba(100,100,100,0.5);
        }

        .sub-title{
            width: 100%;
            color: white;
            letter-spacing: 5px;
            margin: 15px 0;
        }

        .sub-content{
            width: 100%;
            color: white;
            font-size: 1.2em;
        }

        .summary-content{
            text-indent: 2.4em;
        }

        .skill-list{
            margin: 0;
            padding: 0 15px;
            li{
                list-style: none;
                margin: 10px 0;
                padding: 0;
                display: flex;
                align-items: center;
            }
        }
    }


}