.carousel-section{
    width: 95%;
    height: fit-content;
    margin: 0 auto;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    position: relative;


    .scene {
        width: 30vw;
        max-width: 30vh;        
        height: fit-content;
        perspective: 1000px;
        perspective-origin: 50% 50%;
        position: relative;
        padding-bottom: 15vh;
    }

    .carousel-list{
        transform-style: preserve-3d;
        height: fit-content;
 
        .carousel-item{
            width: 100%;
            height: 30vh;
            max-height: 30vh;
            overflow: hidden;
            position: absolute;
            text-align: center;
            backface-visibility: visible;
            display: block;
            box-shadow: 1px 1px 12px gray;
            -moz-transition: -moz-transform 0.3s linear; 
            -webkit-transition: -webkit-transform 0.3s linear; 
            -ms-transition: -ms-transition 0.3s linear;  
            -o-transition: -o-transform 0.3s linear;  
            transition: all 0.3s linear; 
            background-position: center;
            background-size: cover;
            background-repeat: no-repeat;

            .detail{
                width: 100%;
                height: 120%;
                background: linear-gradient(rgba(0,0,0,0.3), rgba(255,255,255,0));            

            }

            &:hover{
                cursor: pointer;
                transform: scale(1.05);
            }
        }

        #carousel-item-0{
            -moz-transition: -moz-transform 0.3s linear; 
            -webkit-transition: -webkit-transform 0.3s linear; 
            -ms-transition: -ms-transition 0.3s linear;  
            -o-transition: -o-transform 0.3s linear;  
            transition: all 0.3s linear; 
            transform: translateY(-5vh) translateZ(300px);
        }
        #carousel-item-1{
            -moz-transition: -moz-transform 0.3s linear; 
            -webkit-transition: -webkit-transform 0.3s linear; 
            -ms-transition: -ms-transition 0.3s linear;  
            -o-transition: -o-transform 0.3s linear;  
            transition: all 0.3s linear; 
            transform: translateY(-10vh) scale(0.9) translateX(80%) translateZ(200px);
        }
        #carousel-item-2{
            -moz-transition: -moz-transform 0.3s linear; 
            -webkit-transition: -webkit-transform 0.3s linear; 
            -ms-transition: -ms-transition 0.3s linear;  
            -o-transition: -o-transform 0.3s linear;  
            transition: all 0.3s linear; 
            transform: translateY(-18vh) scale(0.9) rotateY(180deg)  translateZ(-100px);
        }
        #carousel-item-3{
            -moz-transition: -moz-transform 0.3s linear; 
            -webkit-transition: -webkit-transform 0.3s linear; 
            -ms-transition: -ms-transition 0.3s linear;  
            -o-transition: -o-transform 0.3s linear;  
            transition: all 0.3s linear; 
            transform: translateY(-10vh) scale(0.9) translateX(-80%) translateZ(200px);
        }
    }


}