@import "./../../variable";

#experience-intro-section{
    width: 95%;
    min-height: 100vh;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
    #content{
        width: 90%;
        max-width: 1200px;
        text-align: center;
        
        .graduation-img{
            width: 90%;
            max-width: 600px;
            margin-bottom: 20px;
            box-shadow: 1px 1px 15px rgba(100,100,100,0.5);
        }
        
        .title{
            letter-spacing: "3px";
            color: "#343a40";
            font-size: 3em;
            text-align: center;
            margin-bottom: 20px;
        }
        //電腦
        @media only screen and (min-width: $small-screen-width) {
                .sub-title{
                    font-size: 1.6em;
                    color:"#343a40";
                    margin:10px 0;
                }
        }
        //手機
        @media only screen and (max-width: $small-screen-width) {
                .sub-title{
                    font-size: 1.3em;
                    color:"#343a40";
                    margin:10px 0;
                }
        }
        .logo{
            margin: 10px 0;
            width: 100%;
            height: auto;
            max-width: 300px;

        }
    }

}