@import "./../../variable";

#main-navigation-section{
    display: flex;
    align-items: center;
    min-height: 100vh;
    overflow: auto;
    .content{
        width: 100%;
        max-width: 1200px;
        padding-bottom: 20vh;
        margin: 0 auto;

        .slide-up-link{
            display: block;
            text-align: center;
            margin: 0 auto;
            margin-bottom: 100px;
            text-decoration: none;
            font-size: 28px;
            color: #42bdb9;
            width: fit-content;
            &:hover{
                cursor: pointer;
            }
        }

        .nav{
            margin: 0 auto;
            width: 90%;
            height: 80px;
            background-color: white;
            box-shadow: 1px 1px 5px rgba(100,100,100,0.3);
            margin-top: 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-radius: 0.2em;
            text-decoration: none;
            color: #343a40;
            &:hover{
                cursor: pointer;
            }

            h1{
                margin: 0;
                padding:0;
            }
        }
    }

}

#top-navigation-section{
    width: 100%;
    box-shadow: 1px 1px 10px rgba(100,100,100,0.3);
    position: absolute;
    top: 0;
    left:0;
    z-index: $top-nav-index;
    background-color: white;
    .top{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:10px 20px;

        .avatar{
            width: 30px;
            height: 30px;
            background-size: cover;
            border-radius: 50%;
            margin: 0;
            padding: 0;
        }

        .collapse-checked{
            width: 30px;
            height: 30px;
            opacity: 0;
            &:hover{
                cursor: pointer;
            }
        }
        .collapse-icon{
            position: absolute;
            top: 10px;
            right: 20px;
            font-size: 30px;
            pointer-events: none;
        }
    }

    .menu{ 
        height: 0px;
        overflow: hidden;
        .menu-item{
            width: 100%;
            display: block;
            text-align: center;
            padding: 10px 0;
            font-size: 20px;
            text-decoration: none;
            color: #232b2b;
            &:hover{
                cursor: pointer;
                color: orange;
            }
        }


    }
}

