.home-section{
    height: 100vh;
    width: 100vw;
    background-color: #f7f7f7;
    overflow: hidden;
}

.slide-up{
    animation: slide-up-animate 2s ease ;
}

.slide-down{
    animation: slide-down-animate 2s ease ;
}


.fade-out{
    animation: FadeOut-animate 2s ease ;
}


@keyframes slide-up-animate {
    0% {
        transform: translateY(0vh);
    }
    100% {
        transform: translateY(-100vh);
    }
}

@keyframes slide-down-animate {
    0% {
        transform: translateY(-100vh);
    }
    100% {
        transform: translateY(0vh);
    }
}

@keyframes FadeOut-animate {
    0% {
        transform: translateX(0vw);
    }
    100% {
        transform: translateX(100vw);
    }
}



#home-section {
    scroll-behavior: smooth;
}