#entrypoint-section{
    display: flex;
    align-items: center;
    min-height: 100vh;
    overflow: hidden;
    #content{
        padding-bottom: 10vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        transition: transform 0.4s ease-out;
        .avatar{
            background-color: wheat;
            width: 90vw;
            max-width: 300px;
            height: 300px;
            border-radius: 50%;
            background-size: cover;
            background-repeat: no-repeat;
            background-position: center center;
            box-shadow: 1px 1px 12px rgba(100,100,100,0.5);
        }
    
        .greet-section{
            width: 100vw;
            margin-top: 20px;
            .greet-text{
                margin: 0 auto;
                width: 6em;
                overflow: hidden;
                white-space: nowrap;
                animation: typing 2s steps(6),caret 1s steps(1) infinite;
                border-right: .05em solid;
            }
        }

        .abouts-btn{
            margin-top: 15px;
            display: block;
            text-align: center;
            text-decoration: none;
            width: auto;
            height: 50px;
            background-color: cadetblue;
            border-radius: 0.2em;
            color: white;
            display: flex;
            align-items: center;
            justify-content: center;
            overflow: hidden;
            border: 1px cadetblue solid;
            
            &:hover{
                cursor: pointer;
                box-shadow: 1px 1px 12px rgba(100,100,100,0.5);
            }

            h1{
                animation: center-animate 4s ease alternate;
                overflow: hidden;
                width: 280px;
                // width: 280px;
            }
            .left{
                width: 15px;
                height: 100%;
                background-color: wheat;
            }

            .right{
                width: 15px;
                height: 100%;
                background-color: wheat;
            }
        }

        .prompt{
            width: 100%;
            text-align: center;
            font-size: 24px;
            margin-top: 8px;
            animation:  finger-animate 6s ease alternate;
        }
    
    }



    @keyframes caret {
        50% { border-color: transparent; }
      }

    @keyframes typing {
        from { width: 0 }
    }

    @keyframes finger-animate {
        0% {
            opacity: 0;
        }
        100% {
            opacity: 1;
        }
      }    
      
      @keyframes center-animate {
        0% {
            transform: translateX(-280px);
            width: 0px;
        }
        100% {
            transform: translateX(0px);
            width: 280px;
        }
      }    
      

}